/* InventoryList.css - Command Console Style */
:root {
    --background-color: #1e1e1e;
    --text-color: #00ff00;
    --category-color: #00ffff;
    --white-color: #ffffff;
    --yellow-color: #ffff00;
    --error-color: #ff0000;
    --font-family: 'Courier New', monospace;
}

.inventory-list {
  background-color: var(--background-color);
  font-size: 16px;
  color: var(--text-color);
  font-family: var(--font-family);
  padding: 20px;
  border: 2px solid #00ff00; /* Green border to frame the console */
  border-radius: 0; /* Square corners for a terminal look */
  overflow-x: auto; /* Allow horizontal scrolling if content overflows */
  white-space: pre-wrap; /* Preserve whitespace and wrap text */
  margin-right: auto;
  text-align: left; 
  max-width: 80%;
  margin: 0 auto;
}

.inventory-list h2 {
  color: #00ff00; /* Green title */
  font-size: 1.5em;
  margin-bottom: 15px;
  text-transform: uppercase; /* Optional: Uppercase for title */
}

.inventory-list h3 {
  color: #00ffff; /* Cyan for category headings (another terminal color) */
  font-size: 1.2em;
  margin-top: 15px;
  margin-bottom: 10px;
  text-transform: uppercase; /* Optional: Uppercase for category headings */
}

.inventory-list ul {
  list-style-type: none; /* Remove default list bullets */
  padding-left: 20px; /* Indent item list */
  margin-bottom: 20px;
}

.inventory-list li {
  display: grid; 
  grid-template-columns: 1fr auto;
  grid-column-gap: 15px; 
  align-items: center; 
  width: 100%;  
}

.inventory-list li.button-container { /* New container for buttons */
    display: flex; /* Use flexbox for button alignment */
    gap: 5px; /* Add some space between buttons */
    justify-content: flex-end; /* Align buttons to the right */
}

.inventory-list li strong {
  color: #ffffff; /* White for item names */
  margin-right: 20px;
  flex-grow: 1;
}

.inventory-list li .qty {
  color: var(--yellow-color);
  margin-left: 10px;
  margin-right: 10px;
  justify-self: end;
}

.inventory-list button {
  background-color: transparent; /* Transparent button background */
  color: #00ff00; /* Green button text */
  border: 1px solid #00ff00; /* Green button border */
  padding: 2px 1px;
  font-size: 0.7em;
  margin-left: 5px;
  border-radius: 0; /* Square button corners */
  cursor: pointer;
  font-family: 'Courier New', monospace; /* Monospaced button font */
  justify-self: end;
}

.inventory-list button:focus {
  outline: none;
  background-color: lightblue;
}

.inventory-list input[type="number"]:focus {
    outline: 2px solid var(--category-color);
    outline-offset: 2px;
}

.inventory-list button:hover {
  background-color: #00ff0020; /* Slightly lighter green on hover */
}

.inventory-list button:focus,
.inventory-list input[type="number"]:focus {
    outline: 2px solid #00ffff; /* Cyan focus outline */
    outline-offset: 2px;
}

.alert-danger {
  background-color: #ff000020; /* Slightly transparent red for error alert */
  color: #ff0000; /* Red error text */
  border: 1px solid #ff0000; /* Red border for error alert */
  border-radius: 0;
  padding: 10px;
  margin-bottom: 10px;
  font-family: 'Courier New', monospace; /* Monospaced error font */
}

 #myInput {
    width: 60px; /* Start with a smaller fixed width */
    max-width: 60%; /* Ensure it scales, but doesn't get too large */
    height: 25px; /* Fixed, smaller height */
    padding: 3px; /* Reduced padding */
    font-size: 10px; /* Smaller font */
    border: 1px solid #ccc;
    box-sizing: border-box;
    text-align: center;
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
}

/* Media Queries for Responsiveness */
@media (max-width: 400px) {

  #myInput,
   .inventory-list input[type="number"] {
    width: 40px; /* Take up most of the screen width on smaller screens */
    font-size: 18px; /* Further reduce font size if needed */
  }
  
    /* Adjust font sizes for smaller screens */
  .inventory-list h2 {
        font-size: 1.2em;
    }

  .inventory-list h3 {
        font-size: 1em;
    }

    /* Adjust container width */
  .inventory-list {
        max-width: 95%;
        /* Take up more of the screen width */
        padding: 10px;
        /* Reduce padding */
    }

    /* Adjust list item spacing */
  .inventory-list ul {
        padding-left: 10px;
        /* Reduce list indent */
    }

  .inventory-list li {
    display: block; /* Change from grid to block to stack elements vertically */
    /* grid-template-columns: 1fr auto;  Removed grid template */
    /* grid-column-gap: 10px; Removed grid gap */
    /* align-items: center; Removed align-items as elements are stacked now */
    width: 100%;
    margin-bottom: 15px; /* Add some vertical space between list items */
  }
	
  
	  .inventory-list li strong {
    margin-right: 0; /* Remove right margin for item name as buttons are below now */
    margin-bottom: 10px; /* Add margin below item name to separate from buttons */
    text-align: justify; /* Center the item name above the buttons */
  }
  
  .inventory-list li .qty {
    justify-self: start; /* Reset justify-self as we are not in grid anymore, align left */
    text-align: center; /* Center quantity below item name if needed */
    margin-left: 0; /* Remove left margin */
    margin-right: 0; /* Remove right margin */
    margin-bottom: 5px; /* Add a little space below quantity */
  }
  
   .inventory-list li.button-container { /* Style the button container if you still need it */
    display: block; /* Ensure it stacks vertically if needed */
    text-align: center; /* Center align buttons within container */
    gap: 0; /* Remove gap as we're stacking vertically */
    justify-content: center; /* Center content */
  }

  .inventory-list button {
        font-size: 0.8em;
        /* Smaller font size */
        padding: 3px 6px;
        /* Reduce padding */
		
    }	

}